const switchWrapper = document.querySelector('.switch');
const checkbox = switchWrapper.querySelector('input');
const valueRu = switchWrapper.querySelector('.switch-value_ru');
const valueEn = switchWrapper.querySelector('.switch-value_eng');

switchWrapper.addEventListener('click', () => {
    if (checkbox.checked) {
        valueRu.classList.remove('checked');
        valueEn.classList.add('checked');
    } else {
        valueEn.classList.remove('checked');
        valueRu.classList.add('checked');
    }
})


let coocie = document.querySelector('.coocie');
let coocieTarg = document.querySelector('.coocieTarg');

if (coocie) {
    coocieTarg.addEventListener('click', (e) => {
        e.preventDefault();

        coocie.classList.add('active');
    })
}